fun randomCard(): Card {
    fun rand(max: Int) = MathUtils.randomFloat(0f, max.toFloat()).toInt()

    return when (rand(2)) {
        0 -> MoveCard(
            move = when (rand(5)) {
                0 -> Move.Backward
                else -> Move.Forward
            },
            steps = rand(4) + 1
        )
        else -> RotateCard(
            rotate = when (rand(3)) {
                0 -> Rotate.Left
                1 -> Rotate.Right
                else -> Rotate.UTurn
            }
        )
    }
}


