suspend fun suspendTestingSharedFlow() = Korge(width = 512 * 2, height = 512 * 2, bgcolor = Colors["#2b2b2b"]) {
    println("Start")

    val sharedFlow = MutableSharedFlow<Int>()
    launch {
        repeat(2) {
            delay(1.seconds)
            println("Send $it")
            sharedFlow.emit(it)
        }
        println("Stop sending")
    }

    launch {
        delay(5.seconds)
        println("Start receiving")
        sharedFlow.collect {
            println("Receiveing from emit $it")
        }
    }

    println("End")
}

suspend fun suspendTesting() = Korge(width = 512 * 2, height = 512 * 2, bgcolor = Colors["#2b2b2b"]) {
    println("Start")


    val timerFlow = MutableStateFlow(10)
    launch {
        repeat(30) {
            timerFlow.value = 10 - it
            delay(1.seconds)
        }
    }

    suspendCancellableCoroutine<Unit> { continuation ->
        println("Suspend")
        launch {
            timerFlow.collect { time ->
                println("Starts in $time")
                if (time % 10 == 0) {
                    continuation.resume(Unit)
                    cancel()
                }
            }
        }
    }
    println("End")
}
suspend fun cancelTesting() = Korge(width = 512 * 2, height = 512 * 2, bgcolor = Colors["#2b2b2b"]) {

    val stateFlow = MutableStateFlow(10)
    val signal = Signal<Int>()
    val asyncSignal = AsyncSignal<Int>()


//    val job: Job = launch {
//        repeat(10) {
//            delay(1.seconds)
//            println("Job1 ".plus(10 - it))
//        }
//    }
    val job2: Job = launch {
        stateFlow.collect {
            println("Job2 ".plus(it))
        }
    }
//    val job3: Job = launch {
//        signal {
//            println("Job3 ".plus(it))
//        }
//    }
    val job4: Job = launch {
        asyncSignal {
            println("Job4 ".plus(it))
        }
    }

    launch {
        repeat(10) {
            delay(1.seconds)
            val time = 10 - it
            stateFlow.value = time
            signal(time)
            asyncSignal(time)
        }
    }

    println("Start counting")
    delay(5.seconds)
    println("cancel")
    job4.cancel()

}

object ConfigModule: Module() {
    override val bgcolor = Colors["#2b2b2b"]
    override val mainScene: KClass<out Scene> = Scene1::class
    override val size: SizeInt = SizeInt(1080, 1920)
    override val fullscreen: Boolean = true
    override suspend fun AsyncInjector.configure() {
        mapPrototype { Scene1() }
        mapPrototype { Scene2() }
    }
}

class Scene1() : Scene() {
    override suspend fun Container.sceneMain() {
        val text = text("")

        stage.onMove { event ->
            text.text = "${event.currentPosStage}"
            text.xy(event.currentPosLocal)
            println(event.currentPosLocal)
        }
        stage?.apply {
            println("Width ${this.scaledWidth}")
            println("Height ${this.scaledHeight}")

            println("Game window w ${this.gameWindow.width}")
            println("Game window h ${this.gameWindow.height}")
            println("Window $windowBounds")


            println("Virtual width ${views.virtualWidth}")
            println("Virtual height ${views.virtualHeight}")

            println("Actual Virtual width ${views.actualVirtualWidth}")
            println("Actual Virtual height ${views.actualVirtualHeight}")
        }
    }

    override suspend fun Container.sceneInit() {

        val rect = solidRect(50.0, 50.0, Colors.GREEN).xy(50.0, 50.0)
        rect.onClick {
            sceneContainer.changeTo<Scene2>()
        }
