//        onMove { event ->
//            println("Hi there!!!")
//            text.text = "${event.currentPosLocal}"
//            text.xy(event.currentPosLocal)
//        }

    }
}

class Scene2() : Scene() {
    override suspend fun Container.sceneInit() {
        val rect = solidRect(100.0, 100.0, Colors.RED).xy(100.0, 100.0)
        rect.onClick {
            sceneContainer.changeTo<Scene2>()
        }
    }
}

suspend fun clientSelector() = Korge(width = 512, height = 512  / 2, bgcolor = Colors["#2b2b2b"]) {
    suspend fun server() = tankMain()
    suspend fun client() = playerMain()

    korui {
        vertical {
            button(text = "Server") {
                onClick {
                    launch(Dispatchers.Default) {
                        server()
                    }
                }
            }
            button(text = "Client") {
                onClick {
                    launch(Dispatchers.Default) {
                        client()
                    }
                }
            }
        }
    }
}

