import com.soywiz.korge.view.View
import scenes.GameMapView
import scenes.SuspendGlobalAction
import scenes.SuspendViewAction

interface Action {
    suspend fun GameMapView.perform()
}

class GlobalAction(
    val runBlock: SuspendGlobalAction
) : Action {
    override suspend fun GameMapView.perform() = runBlock.run {
        perform()
    }
}

class PlayerAction(
    val playerId: String,
    val card: Card,
    val view: View,
    val runBlock: SuspendViewAction
) : Action {
    override suspend fun GameMapView.perform() {
        runBlock.run {
            perform(view)
        }
    }
}

