import com.soywiz.korim.bitmap.Bitmap
import com.soywiz.korim.format.readBitmap
import com.soywiz.korio.async.runBlockingNoJs
import com.soywiz.korio.file.VfsFile
import com.soywiz.korio.file.fullName
import com.soywiz.korio.file.std.resourcesVfs

lateinit var bitmaps: Map<String, Bitmap>

suspend fun initBitmaps() {
    bitmaps = listOf(
        "tank/tank_misc.png",
        "tank/icon_rotate_left.png",
        "tank/checkpoints.png",
        "tank/tank.png",
        "tank/icon_up.png",
        "tank/icon_down.png",
        "tank/icon_rotate_left.png",
        "tank/icon_rotate_right.png",
        "tank/icon_rotate_uturn.png",
    ).associateWith {
        resourcesVfs[it].readBitmap()
    }
}

fun VfsFile.readBitmapAlready() {

}

fun VfsFile.readBitmapLazy(): Bitmap {
    return bitmaps[path.substring(1)] ?: error("Could not find bitmap $path")
}