fun Stage.textDialog(
    initialText: String = "",
    textEntered: (text: String) -> Unit
) = container {
    val dialog: Container = this
    val stage = this@textDialog

    stage.sendChildToFront(dialog)

    onDown { it.stopPropagation() }

    solidRect(stage.width + 300, stage.height, Colors.BLACK.withAf(0.7f))
    container {
        val textEnteredSignal = Signal<String>()
        var textState = initialText

        column(
            spacing = 16.0
        ) {
            text("Choose a name", textSize = 32.0)
            row(spacing = 16.0) {
                val uiButtonWidth = 50.0
                container {
                    solidRect(stage.width - uiButtonWidth - 40, 50.0)
                    text(textState, 24.0, Colors.BLACK) {
                        centerOn(this@container)
                        textEnteredSignal { newText ->
                            text = newText
                            centerOn(this@container)
                        }
                    }
                }
                uiButton(width = uiButtonWidth, height = 50.0, text = "OK") {
                    onClick {
                        dialog.removeFromParent()
                        textEntered(textState)
                    }
                }
            }
            keyboard {
                onKeyPressed {
                    textState += it
                    textEnteredSignal(textState)
                }
                onBackPressed {
                    textState = textState.dropLast(1)
                    textEnteredSignal(textState)
                }
            }
        }
        centerOnStage()
    }
}

fun Container.keyboard(block: KeyboardView.() -> Unit) = KeyboardView().addTo(this).also {
    it.init()
    block(it)
}

class KeyboardView: Container() {
    companion object {
        const val BACK_TEXT = "delete"
    }

    private val keymap = listOf(
        "qwertyuiopå".toList(),
        "asdfghjklöä".toList(),
        "zxcvbnm".toList() + BACK_TEXT,
    )

    val onKeyPressed = Signal<String>()
    val onBackPressed = Signal<Unit>()

    fun init() {
        val maxSize = keymap.maxOf { it.size }

        val keyWidth = stage!!.width / maxSize
        val keyHeight = keyWidth

        var y = .0
        keymap.forEach { character ->
            var x = .0
            container {
                character.forEachIndexed { index, char ->
                    val isBackChar = char == BACK_TEXT
                    val w = keyWidth.takeUnless { isBackChar } ?: (keyWidth * 2)

                    solidRect(w, keyHeight, Colors.BLACK.withAf((11f - index) / 11f)) {
                        val container = this
                        xy(x, y)
                        val padding = 4.0

                        val isPressed = Signal<Boolean>()

                        solidRect(w - padding, keyHeight - padding, Colors.DARKGREY) {
                            centerOn(container)
                            isPressed { pressed ->
                                color = Colors.DARKGREEN.takeIf { pressed } ?: Colors.DARKGREY
                            }
                        }
                        text(char.toString(), textSize = 30.0) {
                            centerOn(container)
                        }
                        singleTouch {
                            start {
                                isPressed(true)
                            }
                            endAnywhere {
                                isPressed(false)
                                if (isBackChar) {
                                    onBackPressed(Unit)
                                } else {
                                    onKeyPressed(char.toString())
                                }
                            }
                        }
                    }
                    x += keyWidth
                }
                centerXOnStage()
            }
            y += keyHeight
        }

//        alignTopToBottomOf(stage!!)
    }
}