val resources = mutableMapOf<String, Any>()

fun View.boardPosition(): IPoint {
    val xPos = (if (this is Anchorable) x - scaledWidth * anchorX else x).roundToInt()
    val yPos = (if (this is Anchorable) y - scaledHeight * anchorY else y).roundToInt()
    return IPoint(xPos / stepSize.toInt(), yPos / stepSize.toInt())
}

fun IPoint.toPointInt() = PointInt(x.toInt(), y.toInt())

fun View.nextBoardPosition(moveDirection: Vector2D) = boardPosition() + moveDirection

fun View.facingDirection() = Vector2D(this.rotation.cosine.roundToInt(), this.rotation.sine.roundToInt())

fun Vector2D.rotation() = this.angleTo(IPoint(0, 0))

fun View.advanceInDirection(point: IPoint) {
    x += point.x
    y += point.y
}

fun View.scalePixels(x: Int, y: Int) {
    scale(x / unscaledWidth, y / unscaledHeight)
}

fun IPoint.angleBetween(other: IPoint): Angle {
    return Angle(atan2(other.y, other.x) - atan2(y, x))
}

fun View.setBoardPosition(x: Int, y: Int) {
    this.x = (x * stepSize).toDouble()
    this.y = (y * stepSize).toDouble()

    if (this is Anchorable) {
        this.x -= anchorX * (unscaledWidth * scaleX)
        this.y -= anchorY * (unscaledHeight * scaleY)
    } else {
        this.x += 0.5 * scaledWidth
        this.y += 0.5 * scaledHeight
    }
}

suspend inline fun View.moveBy(point: IPoint, time: TimeSpan = 1.seconds, easing: Easing = Easing.EASE_IN_OUT_QUAD) = tween(this::x[this.x + point.x], this::y[this.y + point.y], time = time, easing = easing)


fun Container.autoSizedInputText(initialText: String, textSize: Double, block: UITextInput.() -> Unit): UITextInput {
    val dummyTextView = text(initialText, textSize).apply {
        visible = false
    }

    return uiTextInput(
        initialText = initialText,
        skin = BoxUISkin(
            bgColor = Colors.TRANSPARENT_BLACK,
            borderColor = Colors.TRANSPARENT_BLACK,
            borderColorOver = Colors.TRANSPARENT_BLACK,
            outlineColor = Colors.TRANSPARENT_BLACK,
            borderColorFocused = Colors.TRANSPARENT_BLACK,
            bgColorFocused = Colors.TRANSPARENT_BLACK
        )
    ) {
        fun updateSize() {
            width = dummyTextView.width + 6
            height = dummyTextView.height + 6
        }

        this.textSize = textSize
        this.textColor = Colors.WHITE
        updateSize()

        onTextUpdated {
            dummyTextView.text = it.text
            updateSize()
        }
    }.also { block(it) }
}

fun <T, Y>MutableMap<Y, T>.swap(lIndex: Y, rIndex: Y) {
    val temp = this[lIndex]!!
    this[lIndex] = this[rIndex]!!
    this[rIndex] = temp
}

inline fun <reified V> Signal<*>.filterInstance(): Signal<V> {
    val out = Signal<V>()
    this.add {
        if (it is V) {
            out(it)
        }
    }
    return out
}

fun Container.column(
    spacing: Double = 0.0,
    block: Container.() -> Unit
) = object: Container() {
    override fun onChildAdded(view: View) {
        if (numChildren > 1) {
            val prevChild = getChildAt(numChildren - 2)
            view.alignTopToBottomOf(prevChild, spacing)
            prevChild.centerXOnStage()
        }
    }
}.also {
    it.addTo(this)
    block(it)
}

fun Container.row(
    spacing: Double = 0.0,
    block: Container.() -> Unit
) = object: Container() {
    override fun onChildAdded(view: View) {
        if (numChildren > 1) {
            val prevChild = getChildAt(numChildren - 2)
            view.alignLeftToRightOf(prevChild, spacing)
        }
    }
}.also {
    it.addTo(this)
    block(it)
}

val View.centerY get() = y + height / 2
val View.centerX get() = x + width / 2
val View.bottom get() = y + height
val View.right get() = x + width