suspend fun playerMain() = Korge(
    width = 480, height = 840, bgcolor = Colors["#2b2b2b"],
) {
    val gameClient = GameClient()

    gameClient.messageSignal { message ->
        // The only thing we want to do here is switch between screens
        if (message is SendCards) {
            removeChildren()
            launch {
                cardHandScreen(
                    initialCards = message.cards,
                    gameClient = gameClient
                )
            }
        }
    }

    clientLobbyScreen(gameClient)

    currentCoroutineContext()[Job]?.invokeOnCompletion {
        println("Window closed $it")
        Dispatchers.Default.launchUnscoped {
            gameClient.close()
        }
    }
}

suspend fun Stage.cardHandScreen(
    initialCards: List<Card>,
    gameClient: GameClient
) {
    // This is some sort of controller... Should this be moved up to playerScene?
    val cardSent = Signal<List<Card>>()
    val cardPlayed = Signal<Card>()
    val cardStopped = Signal<Card>()
    val lockAllCardsSignal = Signal<Boolean>()
    val lockSingleCardsSignal = Signal<Int>()

    gameClient.messageSignal {
        when (it) {
            is SendCards -> cardSent(it.cards)
            is Event -> when (val event = it.event) {
                is LockCards -> lockAllCardsSignal(true)
                is UnlockCards -> lockAllCardsSignal(false)
                is CardStarted -> cardPlayed(event.card)
                is CardEnded -> cardStopped(event.card)
            }
            else -> {}
        }
    }

    var cardHandGettor: () -> List<Card>

    fun onCardsReceived(cards: List<Card>) = launch {
        removeChildren()
        cardHandGettor = cardHand(
            cards = cards,
            cardPlayedSignal = cardPlayed,
            cardStoppedSignal = cardStopped,
            lockAllCardsSignal = lockAllCardsSignal,
            lockSingleCardsSignal = lockSingleCardsSignal,
        )

        uiButton(text = "Send cards") {
            alignBottomToBottomOf(this@cardHandScreen, padding = 32.0)
            centerXOnStage()
            onClick {
                gameClient.send(
                    SendCards(cardHandGettor())
                )
            }
        }
    }

    onCardsReceived(initialCards)

    cardSent { cards ->
        onCardsReceived(cards)
    }
}

val Card.label
    get() = when (this) {
        is MoveCard -> "Move ${move.name} $steps ".plus("step".takeIf { steps == 1 } ?: "steps")
        is RotateCard -> "Rotate ${rotate.name.lowercase()}".takeUnless { rotate == Rotate.UTurn } ?: "Make u-turn"
        else -> ""
    }

val Card.labelSwedish
    get() = when (this) {
        is MoveCard -> when (move) {
            Move.Forward -> "$steps steg framåt"
            Move.Backward -> "$steps steg bakåt"
        }
        is RotateCard -> when(rotate) {
            Rotate.Left -> "Rotera vänster"
            Rotate.Right -> "Rotera höger"
            Rotate.UTurn -> "Gör en u-sväng"
        }
        else -> ""
    }

suspend fun Stage.cardHand(
    cards: List<Card>,
    cardPlayedSignal: Signal<Card>,
    cardStoppedSignal: Signal<Card>,
    lockAllCardsSignal: Signal<Boolean>,
    lockSingleCardsSignal: Signal<Int>,
): () -> List<Card> {
    val stage = this

    val colorCardSelected = Colors.DARKGREEN
    val colorCardUsed = Colors.DARKRED
    val colorCardLocked = Colors.DARKGRAY

    var isAllCardsLocked = false

    class CardView(
        val card: Card,
        override var width: Double,
        override var height: Double
    ): Container() {

        val background = solidRect(width, height, color = Colors.BLACK)

        val text = text(card.labelSwedish, color = Colors.WHITE, textSize = 24.0) {
            centerYOn(background)
            x += 16.0
        }
    }

    val cardViews = mutableMapOf<Card, CardView>()
    val cardPlayed = mutableListOf<Card>()
    val lockedCards = mutableSetOf<Card>()

    fun workingHand() = cardViews.values.sortedBy { it.y }.map { it.card }

    cardPlayedSignal { playedCard ->
        cardViews.entries.forEach { (card, cardView) ->
            if (cardPlayed.contains(card)) {
                cardView.background.color = colorCardUsed
            } else if (playedCard == card) {
                cardView.background.color = colorCardSelected
            }
        }
        cardPlayed += playedCard
    }

    cardStoppedSignal { card ->
        cardViews[card]?.background?.color = colorCardUsed
    }

    lockAllCardsSignal {
        cardViews.values.forEach { cardView ->
            cardView.background.color = colorCardLocked
        }
        isAllCardsLocked = true
    }

    lockSingleCardsSignal.mapSignal { numberOfCardsLocked ->
        workingHand().takeLast(numberOfCardsLocked)
    }.invoke { cardsToBeLocked ->
        lockedCards += cardsToBeLocked
        cardsToBeLocked.map(cardViews::get).forEach {
            it?.background?.color = colorCardLocked
        }
    }

    isAllCardsLocked = false
    cardPlayed.clear()
    removeChildren()

    val cardHeight = 64.0

    val yPositions = cards.mapIndexed { index: Int, card: Card ->
        card to index * (cardHeight + 16.0)
    }.toMap().toMutableMap()

    val cardContainer = container {
        val numberContainerSize = 64.0
        val padding = 32.0
        y = padding
        x = padding

        (0 until 5).forEach { index ->
            solidRect(numberContainerSize, numberContainerSize, color = Colors.WHITE) {
                y = yPositions.values.toList()[index]
                text(text = (index + 1).toString(), color = Colors.BLACK, textSize = 42.0) {
                    centerOn(this@solidRect)
                }
            }
        }

        val cardWidth = stage.width - padding * 3 - numberContainerSize
        cards.forEach { card ->
            addChild(
                CardView(card, cardWidth, cardHeight).apply {
                    x = numberContainerSize + 16
                    cardViews[card] = this
                }
            )
        }
    }

    // animate
    cardViews.entries.forEach { (card, cardView) ->
        val positionY = yPositions[card]!!
        cardView.y = -100.0

        async {
            cardView.moveTo(cardView.x, positionY, time = 0.5.seconds)
        }
    }

    cardViews.entries.forEach { (card, cardView) ->
        cardView.apply {

            onDown {
                cardContainer.sendChildToFront(cardView)
            }

            draggable(autoMove = true) {
                if (lockedCards.contains(card) || isAllCardsLocked) {
                    x -= it.viewDeltaX
                    y -= it.viewDeltaY
                    return@draggable
                }

                if (it.end) {
                    cardView.y = yPositions[card]!!
                }

                x -= it.viewDeltaX

                val startY = yPositions[card]!!

                if (it.viewDeltaY == 0.0) {
                    return@draggable
                }

                val (otherCard, otherCardView) = cardViews.entries.find { (otherCard, otherCardView) ->
                    if (otherCard == card) {
                        return@find false // Don't swap ourselves
                    }

                    if (lockedCards.contains(otherCard)) {
                        return@find false
                    }

                    val otherStartY  = yPositions[otherCard]!!

                    if (it.viewDeltaY < 0) { // Upwards
                        if (otherStartY > startY) {
                            // Filter out cards below our starting position
                            return@find false
                        }

                        cardView.y < otherCardView.centerY
                    } else { // Downwards
                        if (otherStartY < startY) {
                            // Filter out cards above our starting position
                            return@find false
                        }

                        cardView.centerY > otherCardView.y
                    }
                } ?: return@draggable

                yPositions.swap(card, otherCard)

                // Perform animation
                otherCardView.getExtraTyped<Job>("anim")?.cancel()
                val moveJob = launch {
                    // if direction is switched midway, we'll make it faster to settle back to its original position
                    val distance = (otherCardView.y - startY).absoluteValue
                    val percentage = distance / otherCardView.height
                    otherCardView.moveTo(otherCardView.x, startY, time = 200.milliseconds * percentage)
                }
                otherCardView.setExtra("anim", moveJob)
            }
        }
    }

    return {
        workingHand()
    }
}

class CardHandView(
    private val cards: List<Card>
): Container() {
    private val colorCardSelected = Colors.DARKGREEN
    private val colorCardUsed = Colors.DARKRED
    private val colorCardLocked = Colors.DARKGRAY

    private var isAllCardsLocked = false

    class CardView(
        val card: Card,
        override var width: Double,
        override var height: Double
    ): Container() {

        val background = solidRect(width, height, color = Colors.BLACK)

        val text = text(card.toString(), color = Colors.WHITE, textSize = 24.0) {
            centerYOn(background)
            x += 16.0
        }
    }

    private val cardViews = mutableMapOf<Card, CardView>()
    private val cardPlayed = mutableListOf<Card>()
    private val lockedCards = mutableSetOf<Card>()

    fun workingHand() = cardViews.values.sortedBy { it.y }.map { it.card }

    fun cardPlayed(playedCard: Card) {
        cardViews.entries.forEach { (card, cardView) ->
            if (cardPlayed.contains(card)) {
                cardView.background.color = colorCardUsed
            } else if (playedCard == card) {
                cardView.background.color = colorCardSelected
            }
        }
        cardPlayed += playedCard
    }

    fun cardStoppedSignal(card: Card) {
        cardViews[card]?.background?.color = colorCardUsed
    }

    fun lockAllCards() {
        cardViews.values.forEach { cardView ->
            cardView.background.color = colorCardLocked
        }
        isAllCardsLocked = true
    }

    fun lockSingleCards(count: Int) {
        workingHand().takeLast(count).apply {
            lockedCards += this
            map(cardViews::get).forEach {
                it?.background?.color = colorCardLocked
            }
        }
    }

    private val cardHeight = 64.0

    private val yPositions = cards.mapIndexed { index: Int, card: Card ->
        card to index * (cardHeight + 16.0)
    }.toMap().toMutableMap()

    private val padding = 32.0
    private val numberContainerSize = 64.0
    private val cardContainer = container {

        y = padding
        x = padding

        (0 until 5).forEach { index ->
            solidRect(numberContainerSize, numberContainerSize, color = Colors.WHITE) {
                y = yPositions.values.toList()[index]
                text(text = (index + 1).toString(), color = Colors.BLACK, textSize = 42.0) {
                    centerOn(this@solidRect)
                }
            }
        }
    }

    init {
        // Add all child views
        val cardWidth = stage!!.width - padding * 3 - numberContainerSize
        cards.forEach { card ->
            addChild(
                CardView(card, cardWidth, cardHeight).apply {
                    x = numberContainerSize + 16
                    cardViews[card] = this
                }
            )
        }

        // animate
        cardViews.entries.forEach { (card, cardView) ->
            val positionY = yPositions[card]!!
            cardView.y = -100.0

            async(Dispatchers.Default) {
                cardView.moveTo(cardView.x, positionY, time = 0.5.seconds)
            }
        }

        // Card move behavior
        cardViews.entries.forEach { (card, cardView) ->
            cardView.apply {

                onUp {
                    cardView.y = yPositions[card]!!
                }

                onDown {
                    cardContainer.sendChildToFront(cardView)
                }

                draggable(autoMove = false) {
                    if (lockedCards.contains(card) || isAllCardsLocked) return@draggable

                    y -= it.deltaDy

                    val startY = yPositions[card]!!

                    val (otherCard, otherCardView) = cardViews.entries.find { (card, otherCardView) ->
                        if (lockedCards.contains(card)) {
                            return@find false
                        }

                        if (cardView.y > otherCardView.y && otherCardView.y > startY) {
                            return@find true
                        }
                        cardView.y < otherCardView.y && otherCardView.y < startY
                    } ?: return@draggable

                    otherCardView.y = startY
                    yPositions.swap(card, otherCard)
                }
            }
        }
    }
}

suspend fun Stage.clientLobbyScreen(
    gameClient: GameClient
) {
    val stage = this
    val readySignal = Signal<Boolean>()
    val colorSignal = Signal<RGBA>()

    launch(NonCancellable + Dispatchers.Default) {
        // NonCancellable because that allows me to close the socket when the scope is cancelled
        gameClient.connect()
    }

    var currentPlayerName = "PlayerXX"
    var currentColor: Int? = null

    val status = text("Not connected to server") {
        centerXOnStage()
        alignTopToTopOf(stage, padding = 16.0)
        gameClient.connection { connected ->
            text = when (connected) {
                null -> "Connecting to server"
                true -> "Connected to server"
                false -> "Failed to connect to server"
            }
        }
        gameClient.connectionError { error ->
            if (error != null) {
                text = error
            }
        }
        onClick {
            launch(Dispatchers.Default) {
                gameClient.connect()
            }
        }
    }

    val playerNameLabel = text(currentPlayerName, 48.0) {
        centerXOnStage()
        alignTopToTopOf(status, padding = 64.0)
        onClick {
            textDialog(
                initialText = currentPlayerName
            ) {
                if (it.isEmpty()) {
                    return@textDialog
                }
                currentPlayerName = it
                text = it
                centerXOnStage()
                launch {
                    gameClient.send(
                        ConfigurePlayer(
                            name = currentPlayerName,
                            color = currentColor
                        )
                    )
                }
            }
        }
    }

    val tankSprite = Sprite(
        SpriteAnimation(
            spriteMap = resourcesVfs["tank/tank.png"].readBitmap(),
            spriteWidth = 32,
            spriteHeight = 32,
            marginTop = 0,
            marginLeft = 0,
            columns = 10,
            rows = 1,
        )
    ).apply {
        addChild(this)
        scalePixels(200, 200)
        centerXOnStage()
        alignTopToBottomOf(playerNameLabel, padding = 16)
        readySignal { ready ->
            if (ready) {
                playAnimationLooped()
            } else {
                stopAnimation()
            }
        }
        colorSignal {
            colorMul = it
        }
    }

    val swatches = container {
        centerXOnStage()
        alignTopToBottomOf(tankSprite, padding = 32.0)

        fun RGBA.toneDown(value: Float = .1f) = minus(RGBA.float(value, value, value, 0f))

        listOf(
            Colors.RED,
            Colors.GREEN,
            Colors.YELLOW,
            Colors.BLUE.toneDown(.3f),
            Colors.ORANGE,
            Colors.DARKSALMON,
        ).map {
            it.toneDown()
        }.forEachIndexed { index, color ->
            val spacing = 20.0
            val size = 50.0
            val rows = 3
            val column = index % rows
            val row = index / rows
            solidRect(size, size, color) {
                anchor(0.5, 0.5)
                x = (size + spacing) * column - (rows/ 2 * (size + spacing))
                y = (size + spacing) * row
                onClick {
                    tankSprite.colorMul = color
                    currentColor = color.value
                    gameClient.send(
                        ConfigurePlayer(
                            name = currentPlayerName,
                            color = currentColor
                        )
                    )
                }
            }
        }
    }

    var isReady = false
    uiButton(
        text = "Ready"
    ) {
        centerXOnStage()
        alignTopToBottomOf(swatches, padding = 32.0)
        onClick {
            isReady = !isReady
            text = "Ready".takeIf { !isReady } ?: "Unready"
            readySignal(isReady)
            gameClient.send(Ready())
        }
    }
}

suspend fun cardHandTest() = Korge(width = 512 * 2, height = 512 * 2, bgcolor = Colors["#2b2b2b"]) {
    val cardPlayed = Signal<Card>()
    val cardStopped = Signal<Card>()
    val lockAllCardsSignal = Signal<Boolean>()
    val lockSingleCardsSignal = Signal<Int>()

    launch {
        while (true) {
            val cards = (0 until 9).map {
                randomCard()
            }
            removeChildren()
            val cardGetter = cardHand(
                cards = cards,
                cardPlayedSignal = cardPlayed,
                cardStoppedSignal = cardStopped,
                lockAllCardsSignal = lockAllCardsSignal,
                lockSingleCardsSignal = lockSingleCardsSignal,
            )

            lockSingleCardsSignal(2)

            delay(1.seconds)
            lockAllCardsSignal(true)

            println("Receive cards " + cardGetter())
            cardGetter().take(5).forEachIndexed { index, card ->
                delay(0.5.seconds)
                cardPlayed(card)
                delay(0.5.seconds)
                cardStopped(card)
            }
            delay(2.seconds)
        }
    }
}