fun shootAction() = SuspendViewAction { view ->
    val facingDirection = view.facingDirection()
    val gameView: GameMapView = this
    val gameWidth = gameView.width
    val gameHeight = gameView.height
    suspendCancellableCoroutine { continuation ->

        Sprite(
            SpriteAnimation(
                spriteMap = resourcesVfs["tank/tank_misc.png"].readBitmapLazy(),
                spriteWidth = 32,
                spriteHeight = 32,
                marginTop = 32 * 4,
                columns = 1,
                rows = 1
            )
        ).apply {

            fun removeBullet() {
                removeFromParent()
                continuation.resume(Unit)
            }

            scalePixels(50, 50)
            anchor(0.5, 0.5)
            position(view.pos)
            rotation = facingDirection.rotation() - 180.degrees

            onCollision {
                if (it != view && it is Tank) {
                    it.health -= 1
                    removeBullet()
                }
            }

            addUpdater { time ->
                advanceInDirection(facingDirection * 15)
                if (x > gameWidth || x < 0 || y < 0 || y > gameHeight) {
                    removeBullet()
                }
            }

            view.parent?.addChild(this)
        }
    }
}

