fun IPoint.isEqualTo(other: Point): Boolean {
    return x == other.x && y == other.y
}

fun moveAction(moveOption: MoveOption, steps: Int) = SuspendViewAction { view ->
    val moveDirection = moveOption.transform(view.facingDirection())

    suspend fun View.moveAndFall(weAreThePusher: Boolean) {
        val next = nextBoardPosition(moveDirection)

        if (!next.isWithinBounds()) {
            return // Out of bounds
        }

        if (getEntity(next) != null) {
            if (!weAreThePusher) {
                return // This is a tank being pushed and so it can't push another entity
            }

            // Here we are the one pushing a tank. Figure out if it can be pushed.
            val nextNext = next + moveDirection

            if (!nextNext.isWithinBounds()) {
                return // Out of bonds
            }
            if (getEntity(nextNext) != null) {
                return // To heavy to push two tanks
            }
        }

        moveBy(moveDirection * stepSize)
        if (isPitfall(boardPosition())) {
            scaleTo(0, 0)
            removeFromParent()
        }
    }

    (0 until steps).map { stepIndex ->
        if (view.parent == null) // We've died in between a step
            return@map

        val destination = view.nextBoardPosition(moveDirection)
        joinAll(
            async {
                (view as Tank).setMovementIcons(moveOption = moveOption, stepsLeft = steps - stepIndex - 1)
                view.moveAndFall(weAreThePusher = true)
                touchTile(view)
            }, // Us
            async {
                getEntity(destination)?.let { entity ->
                    entity.moveAndFall(weAreThePusher = false)
                    touchTile(entity)
                }
            } // Pushed object
        )
        delay(0.25.seconds)
        (view as Tank).clearIcons()
    }
}

fun conveyorsTurn() = SuspendGlobalAction {

//    println("Entities board pos ".plus(
//        entities.map { it.boardPosition() }
//    ))

    fun findConveyorTile(point: IPoint) = getTile<ConveyorTile>(point)

    joinAll(
        *entities.map { entity ->
            val conveyorTile = findConveyorTile(entity.boardPosition())?.takeIf {
                entity.nextBoardPosition(it.moveDirection()).isWithinBounds()
            }

//            println("Find layer tile " + conveyorTile)
            async {
                if (conveyorTile != null) {
                    val moveDir = when (conveyorTile) {
                        is ConveyorTile.Straight -> conveyorTile.direction
                        is ConveyorTile.Turn -> conveyorTile.outDirection
                    }
                    val movement = moveDir * stepSize
                    val nextConveyorTile = findConveyorTile(entity.nextBoardPosition(moveDir))
                    val beltTime = 1.2.seconds

                    joinAll(
                        async {
                            entity.moveBy(movement.x, movement.y, time = beltTime, Easing.LINEAR)
                            touchTile(entity)
                            if (isPitfall(entity.boardPosition())) {
                                entity.scaleTo(0, 0)
                                entity.removeFromParent()
                            }
                        },
                        async {
                            if (nextConveyorTile is ConveyorTile.Turn && nextConveyorTile.inDirection.isEqualTo(moveDir)) {
                                val rotateAngle = moveDir.angleBetween(nextConveyorTile.outDirection)
                                entity.apply {
                                    tween(this::rotation[rotation + rotateAngle], time = beltTime, easing = Easing.LINEAR)
                                }
                            }
                        }
                    )
                }
            }
        }.toTypedArray()
    )
}

fun shootTurn() = SuspendGlobalAction {
    entities.map { view ->
        async { shootAction().run { perform(view) } }
    }.joinAll()
}