package net

import NetMessage
import PlayerDisconnected
import PlayerJoined
import ServerJoined
import YouAreAnObserver
import com.soywiz.korio.async.Signal
import com.soywiz.korio.async.launch
import com.soywiz.korio.net.ws.WebSocketClient
import com.soywiz.korio.net.ws.readString
import decodeFromString
import encodeToString
import kotlinx.coroutines.Dispatchers
import kotlinx.coroutines.flow.*

val port = 7172
//val host = "192.168.2.107"
//val host = "127.0.0.1"
val host = "178.62.79.134"

class GameClient {

    val connection = Signal<Boolean?>()
    val connectionError = Signal<String?>()
    val messageSignal = Signal<NetMessage>()

    var socket: WebSocketClient? = null
    var myId: String? = null

    suspend fun connect() {
        try {
            connection(null)
            connectionError(null)
            socket = WebSocketClient(
                url = "ws://$host:$port/ws",
            ).apply {
                myId = readString()
                println("Client received id $myId")
                onStringMessage {
                    val (_, toUser, message) = it.getResponse()
                    if (toUser == myId || toUser.isEmpty()) {
                        // if user is empty it is a broadcast, any issues with receiving this?
                        println("Client received message $it")
                        messageSignal(message)
                    }
                }
                onError {
                    println("Client error $it")
                    it.printStackTrace()
                }
                onClose {
                    println("Client closed $it")
                }
            }
            send(PlayerJoined())
            connection(true)
        } catch (ex: Exception) {
            println("Failed to connect to server $ex")
            ex.printStackTrace()
            connection(false)
            connectionError(ex.message)
        }
    }

    suspend fun send(message: NetMessage) {
        socket?.sendMessage(message, from = myId!!)
    }

    suspend fun close() {
        println("Try close on socket $socket ")
        send(PlayerDisconnected())
        socket?.close()
    }
}

class ServerGameClient {
    private val otherServers = mutableSetOf<String>()

    private val _messageFlow = MutableSharedFlow<Pair<String, NetMessage>>()
    val messageFlow = _messageFlow.filter { (id, _) -> !otherServers.contains(id) }

    val playerJoinedFlow = messageFlow.map { (playerId, message) ->
        playerId.takeIf { message is PlayerJoined }
    }.filterNotNull()
    val playerDisconnectedFlow = messageFlow.map { (playerId, message) ->
        playerId.takeIf { message is PlayerDisconnected }
    }.filterNotNull()

    private var socket: WebSocketClient? = null
    var myId: String? = null
    var isObserverServer = false

    suspend fun connect() {
        if (socket != null) return
        try {
            socket = WebSocketClient(
                url = "ws://$host:$port/ws",
            ).apply {
                myId = readString()
                onStringMessage {
                    println("Server received message $it")
                    val (from, _, message) = it.getResponse()
                    launch(Dispatchers.Default) {
                        _messageFlow.emit(from to message)
                    }
                }
                onError {
                    println("Server failed $it")
                    it.printStackTrace()
                }
            }
            broadcast(ServerJoined())
            println("Server is setup")
        } catch (ex: Exception) {
            println("Failed to connect to server $ex")
        }
        launch(Dispatchers.Default) {
            messageFlow.collect { (id, message) ->
                if (message is ServerJoined) {
                    otherServers += id // Interesting... There are other servers. Make sure we don't process their data.

                    // Say hello back, let them know since we joined first they can only observe... Or else we'll have two
                    // servers sending cards to player
                    send(YouAreAnObserver(), id)
                } else if (message is YouAreAnObserver) {
                    isObserverServer = true

                    otherServers += id // Add them to the ignore list
                }
            }
        }
    }

    suspend fun send(message: NetMessage, playerId: String) {
        if (!isObserverServer) {
            socket?.sendMessage(message, to = playerId)
        }
    }

    suspend fun send(message: NetMessage, playerIds: Collection<String>) {
        if (!isObserverServer) {
            playerIds.forEach {
                socket?.sendMessage(message, to = it)
            }
        }
    }

    suspend fun broadcast(message: NetMessage) {
        if (!isObserverServer) {
            socket?.sendMessage(message, to = "")
        }
    }
}

suspend fun WebSocketClient.sendMessage(message: NetMessage, from: String = "", to: String = "") {
    send(
        from.plus("->").plus(to).plus("#").plus(message.encodeToString())
    )
}

private fun String.getResponse(): Response {
    // $from#$to@$data
    // client makes a broadcast user123->#...data...
    // server makes a broadcast ->#...data...
    val fromUser = substringBefore("->")
    val toUser = substringBefore("#").substringAfter("->")
    val content = substringAfter("#")
    return Response(
        fromUser,
        toUser,
        content.decodeFromString()
    )
}

data class Response(
    val from: String,
    val to: String,
    val message: NetMessage
)