fun interface SuspendGlobalAction {
    suspend fun GameMapView.perform()
}

fun interface SuspendViewAction {
    suspend fun GameMapView.perform(view: View)
}

const val stepSize = 75.0

class SpawnTile(
    val index: Int
)

class GameMapView(
    override val stage: Stage,
    val tiledMap: TiledMap,
    val tanksStartRotation: Angle = (-90).degrees,
    callback: GameMapView.() -> Unit
) : Container(), CoroutineScope by stage {

    class TileTransform(
        val tilesetName: String,
        val xRange: IntRange,
        val yRange: IntRange,
        val transform: Tile.() -> Any?
    )

    val tanks: List<Tank>
        get() = entities

    val entities: List<Tank>
        get() = (0 until numChildren).map { getChildAt(it) }.filterIsInstance<Tank>()

    private val tileTransforms = mutableListOf<TileTransform>()

    private val checkpointBounds: List<Rectangle> = tiledMap.objectLayers.map { objects: TiledMap.Layer.Objects ->
        objects.objects.mapNotNull { tileObject ->
            if (tileObject.name.startsWith("Spawn")) {
                tileObject.name to tileObject.bounds
            } else null
        }
    }.flatten().toMap().entries.sortedBy { (name, _) -> name }.map { (_, bounds) -> bounds } // Take bounds only, I don't care about names

    private val checkpoints: List<Set<PointInt>> = checkpointBounds.map {
        (it / stepSize).run {
            (x.toInt() until (x + width).toInt()).map { x ->
                (y.toInt() until (y + height).toInt()).map { y ->
                    println(PointInt(x, y))
                    PointInt(x, y)
                }
            }.flatten().toSet()
        }
    }
    private val spawnPoints = mutableMapOf<String, PointInt>()
    private val checkpointViews = mutableMapOf<PointInt, CheckpointView>()

    fun touchTile(tank: Tank) {
        val pointInt = tank.boardPosition().toPointInt()
        val checkpointIndex = checkpoints.indexOfFirst { it.contains(pointInt) }
        val currentCheckpointIndex = getTankCheckpointIndex(tank)
        if (checkpointIndex == currentCheckpointIndex + 1) {
            println("Touched new spawn")
            spawnPoints[tank.name!!] = checkpoints[checkpointIndex].first()
            checkpointViews[pointInt]?.addColor(tank.color)
        }
    }

    private fun getTankCheckpointIndex(tank: Tank) = checkpoints.indexOfFirst {
        it.contains(spawnPoints[tank.name]) // If a tank has a spawn inside a checkpoint we can assume they've taken it
    }

    fun getWinners(): Set<Tank> = entities.associateWith { tank ->
        getTankCheckpointIndex(tank)
    }.filterValues { checkpoints.lastIndex == it }.keys

    fun respawnEntity(name: String) {
        if (hasTank(name)) return
        val point = spawnPoints[name] ?: return
        Tank().apply {
            setBoardPosition(point.x, point.y)
            this.name = name
        }.also {
            addChild(it)
        }
    }

    fun spawnEntity(name: String, spawnIndex: Int) {
        if (hasTank(name)) return
        println("Get spawn tiles ".plus(getTiles<SpawnTile>()))
        val point = getTiles<SpawnTile>().entries.first { (_, tile) -> tile.index == spawnIndex }.key
        Tank().apply {
            setBoardPosition(point.x, point.y)
            rotation = tanksStartRotation
            this.name = name
        }.also {
            addChild(it)
        }
        spawnPoints[name] = PointInt(point.x, point.y)
    }

    init {
        addChild(TiledMapView(tiledMap, showShapes = false))

        checkpointBounds.forEachIndexed { index, rectangle ->
            CheckpointView(rectangle.width, rectangle.height, frame = index).also {
                it.x = rectangle.x
                it.y = rectangle.y
                addChild(it)
                (rectangle / stepSize).run {
                    (x.toInt() until (x + width).toInt()).forEach { x ->
                        (y.toInt() until (y + height).toInt()).forEach { y ->
                            checkpointViews[PointInt(x, y)] = it
                        }
                    }
                }
            }
        }

        tileTransforms += TileTransform(
            "conveyer_and_misc",
            xRange = 0..1,
            yRange = 0..1
        ) {
            when (frameY) {
                0 -> ConveyorTile.Straight(frameX + 1, transform(Vector2D.Up))
                1 -> ConveyorTile.Turn(frameX + 1, transform(Vector2D.Up), transform(Vector2D.Left))
                else -> null
            }
        }

        tileTransforms += TileTransform(
            "conveyer_and_misc",
            xRange = 3..6,
            yRange = 3..4
        ) {
            val frameXZeroBased = frameX - 3
            val frameYZeroBased = frameY - 3
            SpawnTile(frameYZeroBased % 3 + frameXZeroBased)
        }

        onDown { event ->
            val x = (event.currentPosLocal.x / stepSize).toInt()
            val y = (event.currentPosLocal.y / stepSize).toInt()

            println("$x, $y")

            if (Point(x, y).isWithinBounds()) {
                println(
                    tileLayers.joinToString(separator = ", ") { tiles ->
                        tiles[x, y].toString()
                    }
                )
                println(
                    tileData.joinToString(separator = ", ") { tiles ->
                        tiles[x, y].toString()
                    }
                )
                println("-----")
            }
        }

        callback(this)
    }

    fun getTanks(names: Set<String>): List<Tank> = names.map { playerName ->
        entities.first { it.name == playerName }
    }

    fun getTank(name: String) = entities.first { it.name == name }
    fun hasTank(name: String) = entities.firstOrNull { it.name == name } != null

    private val tileLayers: List<Array2<Tile>> = tiledMap.getTileLayerData()
    val tileData = tileLayers.map { tiles ->
        Array2.withGen(tiles.width, tiles.height) { x, y ->
            val tile = tiles[x, y]
            tileTransforms.filter {
                tile.tileSetName == it.tilesetName && tile.frameX in it.xRange && tile.frameY in it.yRange
            }.also {
                if (it.size > 1) error("Multiple matches")
            }.firstOrNull()?.transform?.invoke(tile) ?: tile
        }
    }

    inline fun <reified T>getTile(point: IPoint): T? = tileData.mapNotNull { tiles ->
        tiles[point] as? T
    }.firstOrNull()

    inline fun <reified T>getTiles(): Map<PointInt, T> = tileData.map { tiles ->
        tiles.map2 { x, y, v ->
            println("Map $x $y to $v")
            PointInt(x, y) to v as? T
        }.toList()
    }.flatten().mapNotNull { (a: PointInt, b: T?) -> b?.let { a to it } }.toMap()

    fun IPoint.isWithinBounds(): Boolean {
        if (x < 0 || y < 0) {
            return false
        }
        return x < tiledMap.width && y < tiledMap.height
    }

    fun isPitfall(point: IPoint) = tileLayers.any { it[point].tileSetName == "pits" }

    fun IPoint.blockingEntity() = entities.firstOrNull { it.boardPosition() == this }

    fun getEntity(point: IPoint) = entities.firstOrNull { point == it.boardPosition() }
}

