enum class Turn {
    PlayerMove,
    PlayerShoot,
    ConveyorMove,
    None,
}

suspend fun tankMain() = Korge(width = 512 * 2, height = 750, bgcolor = Colors["#2b2b2b"]) {
    fun Key.isPressed(): Boolean = input.keys[this]

    val gameClient = ServerGameClient().apply {
        launch(Dispatchers.Default) {
            connect()
        }
    }

    val gameMapView = GameMapView(
        stage = this,
        tiledMap = resourcesVfs["tank/map/board4.tmx"].readTiledMap()
    ) {
        this@Korge.addChild(this)
        centerOnStage()
        debugMode()
    }

    val centerText = Signal<String>()
    val turnState = Signal<Turn>()

    korui {
        vertical {
            Turn.values().forEach { turn ->
                text(
                    text = when (turn) {
                        Turn.PlayerMove -> "All Players move"
                        Turn.PlayerShoot -> "All players shoot"
                        Turn.ConveyorMove -> "Conveyor belts move"
                        Turn.None -> ""
                    },
                    textSize = 24.0,
                    alignment = TextAlignment.LEFT,
                    autoScaling = true
                ) {
                    y = index * (scaledHeight + 10)
                    turnState { currentTurn ->
                        visible = currentTurn != Turn.None
                        color = Colors.RED.takeIf { turn == currentTurn } ?: Colors.WHITE
                    }
                }
            }
        }

        horizontal {
            text(
                text = "Hello there",
                textSize = 32.0,
                alignment = TextAlignment.CENTER
            ) {
                centerText { text ->
                    this.text = text
                }
                centerXOn(gameMapView)
            }
        }
    }

    val players = mutableSetOf<String>()
    val playerConfigurations = mutableMapOf<String, ConfigurePlayer>()

    println("In lobby")
    // Lobby state
    suspendCancellableCoroutine<Unit> { continuation ->

        val playersReady = mutableSetOf<String>()

        launch {
            val outmostLaunch = this

            launch {
                gameClient.playerJoinedFlow.collect { playerId ->
                    println("Player joined")
                    players += playerId
                    centerText("Player joined $playerId")
                    gameMapView.spawnEntity(playerId, players.size - 1)
                }
            }

            launch {
                gameClient.playerDisconnectedFlow.collect { playerId ->
                    println("Player disconnected")
                    players -= playerId
                    centerText("Player disconnected $playerId")
                    gameMapView.getTank(playerId).removeFromParent()
                }
            }

            var gameCountDown: Job? = null

            gameClient.messageFlow.collect { (playerId, message) ->
                if (message is Ready) {
                    val tank = gameMapView.getTank(playerId)
                    if (!playersReady.contains(playerId)) {
                        playersReady += playerId
                        tank.playAnim()
                    } else {
                        playersReady -= playerId
                        tank.stopAnim()
                    }
                    println("Player ready $playerId")
                    println("Players: ${players} and $playersReady are ready")
                    if (playersReady.size > 0 && playersReady == players) {
                        println("All players ready")
                        gameCountDown = launch {
                            try {
                                (0 .. 2).forEach { time ->
                                    centerText("Round starts in ${2 - time}")
                                    delay(1.seconds)
                                }
                                continuation.resume(Unit)
                                outmostLaunch.cancel()
                            } finally {
                                centerText("")
                            }
                        }
                    } else {
                        gameCountDown?.cancel()
                    }
                } else if (message is ConfigurePlayer) {
                    val tank = gameMapView.getTank(playerId)
                    playerConfigurations[playerId] = message
                    tank.applyConfiguration(message)
                }
            }
        }
    }

    println("Game started")
    gameMapView.getTanks(players).forEach {
        it.stopAnim() // stop ready animation
    }

    var timesRun = -1
    do { // Start of a turn
        timesRun++
        // Here we revive all
        println("Starting turn ${timesRun + 1}")

        // Respawn any tanks
        players.forEach { playerId ->
            if (!gameMapView.hasTank(playerId)) {
                gameMapView.respawnEntity(playerId)
                playerConfigurations[playerId]?.let(gameMapView.getTank(playerId)::applyConfiguration)
            }
        }

        // Send cards
        val playerCardsDealt = mutableMapOf<String, List<Card>>()
        val playerCardsReceived = mutableMapOf<String, List<Card>>()

        players.forEach { playerId ->
            val cards = (0 until 9).map { randomCard() }
            println("Send cards $cards")
            gameClient.send(SendCards(cards), playerId)
            playerCardsDealt[playerId] = cards
        }

        suspendCancellableCoroutine<Unit> { continuation ->
            launch {
//                async {
//                    val delay = when (timesRun) {
//                        in 0 until 4 -> 100
//                        in 5 until 9 -> 90
//                        in 10 until 12 -> 80
//                        else -> 60
//                    }
//                    println("Wait $delay seconds")
//                    (0 .. delay).forEach { time ->
//                        centerText("Round starts in ${delay - time}")
//                        delay(1.seconds)
//                    }
//                    println("Wait finished")
//
//                    // If a player has not sent their cards, just take what they were dealt originally
//                    players.forEach { playerId ->
//                        if (playerCardsReceived[playerId] == null) {
//                            playerCardsReceived[playerId] = playerCardsDealt[playerId].orEmpty()
//                        }
//                    }
//
//                    continuation.resume(Unit)
//                    cancel()
//                }
                async {
                    centerText("Waiting on cards: ${playerCardsReceived.size}/${players.size}" )
                    gameClient.messageFlow.filterNotNull().collect { (playerId, message) ->
                        if (message is SendCards) {
                            println("Received player cards")
                            centerText("Received player cards $playerId")
                            playerCardsReceived[playerId] = message.cards
                            gameMapView.getTank(playerId).playAnim()
                        }
                        println("Check if we received all cards. " + playerCardsReceived.keys + " has sent cards out of " + players)
                        if (playerCardsReceived.keys == players) {
                            println("All cards received")
                            centerText("")
                            continuation.resume(Unit)
                            cancel()
                        }
                    }
                }
            }
        }

        println("Move on to play all cards")
        gameMapView.tanks.forEach { it.stopAnim() } // Stop ready anim

        fun cardToAction(playerId: String, view: View, card: Card): PlayerAction {
            return PlayerAction(
                playerId = playerId,
                card = card,
                view = view,
                runBlock = when (card) {
                    is MoveCard -> moveAction(
                        when (card.move) {
                            Move.Forward -> MoveOption.Forward
                            Move.Backward -> MoveOption.Backwards
                        },
                        card.steps
                    )
                    is RotateCard -> rotateAction(
                        when (card.rotate) {
                            Rotate.Left -> Rotation.Left
                            Rotate.Right -> Rotation.Right
                            Rotate.UTurn -> Rotation.Uturn
                        }
                    )
                    else -> error("")
                }
            )
        }

        val playerActions: Map<String, List<PlayerAction>> = playerCardsReceived.mapValues { (playerId, cards) ->
            val tank = gameMapView.getTank(playerId)
            cards.map { card ->
                cardToAction(playerId, tank, card)
            }
        }

        val cardTurns: List<List<PlayerAction>> = (0 until 5).map { index ->
            playerActions.values.map { cardList ->
                cardList[index]
            }
        }

        println("All players sent their cards. Perform their turns now")

        gameClient.send(
            message = Event(
                LockCards()
            ),
            players
        )

        cardTurns.forEach { actionList ->
            gameMapView.run {
                // All player do their first card
                println("Act cards")
                turnState(Turn.PlayerMove)
                actionList.forEach { action ->
                    if (action.view.parent == null) return@forEach
                    gameClient.send(
                        message = Event(
                            CardStarted(action.card)
                        ),
                        playerId = action.playerId
                    )
                    action.run {
                        perform()
                    }
                    gameClient.send(
                        message = Event(
                            CardEnded(action.card)
                        ),
                        playerId = action.playerId
                    )
                }
                // All shoot
                println("Shoot")
                turnState(Turn.PlayerShoot)
                shootTurn().run { perform() }

                // Conveyor belt
                println("Conveyor")
                turnState(Turn.ConveyorMove)
                conveyorsTurn().run { perform() }
            }
        }

        turnState(Turn.None)
    } while (
        gameMapView.getWinners().isEmpty()
    )

    centerText("Congratulations " + gameMapView.getWinners().joinToString { it.name.toString() } + "!")
}

class CheckpointView(
    val rectWidth: Double,
    val rectHeight: Double,
    val frame: Int
): Container() {

    private val colors = mutableListOf<RGBA>()
    private val colorsSignal = Signal<List<RGBA>>()

    fun addColor(color: RGBA) {
        colors += color
        colorsSignal(colors)
    }

    init {
        solidRect(rectWidth, rectHeight, color = Colors.BLACK) {
            alpha(0.30f)
        }
//        roundRect(
//            width = rectSize,
//            height = rectSize,
//            rx = 0.0,
//            fill = Colors.BLACK.withAd(0.2),
//            stroke = Colors.DARKGRAY.withAd(0.6),
//            strokeThickness = 4.0
//        ) {
//            xy(strokeThickness / 2, strokeThickness / 2)
//        }

        container {
            colorsSignal { colors ->
                removeChildren()
                val strokeThickness = kotlin.math.min(rectWidth, rectHeight) / 15
                colors.forEachIndexed { index, rgba ->
                    if (rectWidth == rectHeight) {
                        val coloredRectSize = rectWidth + 1 - (index * strokeThickness) * 2
                        circle(
                            coloredRectSize / 2,
                            strokeThickness = strokeThickness,
                            stroke = rgba,
                            fill = Colors.TRANSPARENT_WHITE
                        ) {
                            alpha(0.25f)
                            x += strokeThickness / 2 + index * strokeThickness
                            y += strokeThickness / 2 + index * strokeThickness
                        }
                    } else {
                        roundRect(
                            rectWidth,
                            rectHeight,
                            20.0,
                            stroke = rgba,
                            strokeThickness = strokeThickness,
                            fill = Colors.TRANSPARENT_WHITE
                        ) {
                            x += strokeThickness / 2 + index * strokeThickness
                            y += strokeThickness / 2 + index * strokeThickness
                            alpha(0.2f)
                        }
                    }
                }
            }
        }

        Sprite(
            SpriteAnimation(
                resourcesVfs["tank/checkpoints.png"].readBitmapLazy(),
                spriteWidth = 75,
                spriteHeight = 75,
                columns = 4,
                rows = 4
            )
        ).apply {
            val imageSize = MathUtils.clamp(kotlin.math.min(rectWidth, rectHeight).toFloat() / 2, 50f, 75f).toInt()
            scalePixels(imageSize, imageSize)
            centerOn(this@CheckpointView)
            x -= 0 * (rectWidth / 75)
            y -= 0 * (rectHeight / 75)
            setFrame(frame)
            this@CheckpointView.addChild(this)
            alpha(0.75)
        }
    }
}

fun GameMapView.debugMode(debug: Boolean = true) {
    if (!debug) return

    fun Key.isPressed(): Boolean = stage.input.keys[this]

    val stateFlowActions = MutableStateFlow<List<Action>>(emptyList())

    keys {
        fun steps() = 2.takeIf { Key.LEFT_SHIFT.isPressed() } ?: 1

        val player1 = "player1"
        val player2 = "player2"

        down(Key.F1) {
            spawnEntity(player1, 0)
        }

        down(Key.F2) {
            spawnEntity(player2, 1)
        }

        down(Key.F5) {
            respawnEntity(player1)
            respawnEntity(player2)
        }

        fun playerAction(playerName: String, suspendAction: SuspendViewAction) {
            if (!hasTank(playerName)) return

            PlayerAction(
                playerName,
                randomCard(),
                getTank(playerName),
                suspendAction
            ).let {
                stateFlowActions.value += it
            }
        }

        // Player 1 movement
        listOf(
            Key.W to player1 to { moveAction(MoveOption.Forward, steps = steps()) },
            Key.S to player1 to { moveAction(MoveOption.Backwards, steps = steps()) },
            Key.A to player1 to { rotateAction(Rotation.Left) },
            Key.D to player1 to { rotateAction(Rotation.Right) },
            Key.SPACE to player1 to { shootAction() },
        ).forEach { (pair1, action: () -> SuspendViewAction) ->
            val (key, playerName) = pair1
            down(key) {
                playerAction(playerName, action())
            }
        }

        // Player 2 movement
        listOf(
            Key.UP to player2 to { moveAction(MoveOption.Forward, steps = steps()) },
            Key.DOWN to player2 to { moveAction(MoveOption.Backwards, steps = steps()) },
            Key.LEFT to player2 to  { rotateAction(Rotation.Left) },
            Key.RIGHT to player2 to { rotateAction(Rotation.Right) },
            Key.RETURN to player2 to { shootAction() },
        ).forEach { (pair1, action: () -> SuspendViewAction) ->
            val (key, playerName) = pair1
            down(key) {
                playerAction(playerName, action())
            }
        }

        down(Key.N1) {
            stateFlowActions.value += GlobalAction(conveyorsTurn())
        }
        down(Key.N2) {
            stateFlowActions.value += GlobalAction(shootTurn())
        }
    }

    launch {
        stateFlowActions.collect { actions ->
            actions.forEach { action: Action ->
                action.run {
                    perform()
                }
            }
            stateFlowActions.value -= actions
        }
    }
}

enum class Rotation(val angle: Angle) {
    Left((-90).degrees),
    Right(90.degrees),
    Uturn((-180).degrees)
}

enum class MoveOption {
    Forward,
    Backwards;

    fun transform(direction: Vector2D): Vector2D {
        return when (this) {
            Forward -> direction
            Backwards -> direction * -1
        }
    }
}

