import com.soywiz.korma.geom.Vector2D

sealed class ConveyorTile {
    data class Straight(
        val steps: Int,
        val direction: Vector2D
    ): ConveyorTile()

    data class Turn(
        val steps: Int,
        val inDirection: Vector2D, // TODO: Can be multiple
        val outDirection: Vector2D
    ): ConveyorTile()

    fun moveDirection() = when (this) {
        is Straight -> direction
        is Turn -> outDirection
    }
}