import com.soywiz.korio.util.UUID
import kotlinx.serialization.*
import kotlinx.serialization.json.Json
import kotlinx.serialization.modules.SerializersModule
import kotlinx.serialization.modules.polymorphic
import kotlinx.serialization.modules.subclass


val module = SerializersModule {
    polymorphic(EventType::class) {
        subclass(LockCards::class)
        subclass(UnlockCards::class)
        subclass(CardStarted::class)
        subclass(CardEnded::class)
    }
    polymorphic(NetMessage::class) {
        subclass(SendCards::class)
        subclass(ConfigurePlayer::class)
        subclass(Event::class)
        subclass(Ready::class)
        subclass(PlayerJoined::class)
        subclass(ServerJoined::class)
        subclass(YouAreAnObserver::class)
        subclass(PlayerDisconnected::class)
    }
    polymorphic(Card::class) {
        subclass(MoveCard::class)
        subclass(RotateCard::class)
    }
}

val format = Json { serializersModule = module }

fun String.decodeFromString() = format.decodeFromString<NetMessage>(this)
fun NetMessage.encodeToString() = format.encodeToString<NetMessage>(this)

interface NetMessage

@Serializable
@SerialName("sendCards")
data class SendCards(val cards: List<Card>): NetMessage

@Serializable
@SerialName("configurePlayer")
data class ConfigurePlayer(
    val name: String? = null,
    val color: Int? = null
): NetMessage

@Serializable
@SerialName("event")
data class Event(val event: EventType): NetMessage

@Serializable
@SerialName("ready")
data class Ready(val ready: Boolean = true): NetMessage

@Serializable
@SerialName("playerJoined")
data class PlayerJoined(val playerId: String? = null): NetMessage

@Serializable
@SerialName("serverJoined")
class ServerJoined: NetMessage

@Serializable
@SerialName("youAreAnObserver")
class YouAreAnObserver: NetMessage

@Serializable
@SerialName("playerDisconnected")
class PlayerDisconnected: NetMessage

interface EventType
@Serializable
@SerialName("lockCards")
data class LockCards(val hello: String = ""): EventType

@Serializable
@SerialName("unlockCards")
class UnlockCards: EventType

@Serializable
@SerialName("cardStarted")
data class CardStarted(val card: Card): EventType

@Serializable
@SerialName("cardEnded")
data class CardEnded(val card: Card): EventType

interface Card
@Serializable
@SerialName("moveCard")
data class MoveCard(val move: Move, val steps: Int, val id: String = UUID.randomUUID().toString()) : Card
@Serializable
@SerialName("rotateCard")
data class RotateCard(val rotate: Rotate, val id: String = UUID.randomUUID().toString()) : Card
