import com.soywiz.klock.milliseconds
import com.soywiz.klock.seconds
import com.soywiz.korge.tween.get
import com.soywiz.korge.tween.tween
import com.soywiz.korge.view.*
import com.soywiz.korim.bitmap.slice
import com.soywiz.korim.color.Colors
import com.soywiz.korim.color.RGBA
import com.soywiz.korim.format.readBitmap
import com.soywiz.korio.async.Signal
import com.soywiz.korio.file.std.resourcesVfs
import com.soywiz.korma.geom.Point
import com.soywiz.korma.geom.degrees
import com.soywiz.korma.geom.plus
import com.soywiz.korma.geom.unaryMinus
import scenes.MoveOption
import scenes.Rotation

class Tank: Container() {
    val maxHealth = 5
    var health = maxHealth
        set(value) {
            field = value
            alpha = (health / maxHealth.toDouble()) / 2 + 0.5
            if (health <= 0) {
                removeFromParent()
            }
        }

    var color: RGBA
        get() = tankSprite.colorMul
        set(value) {
            tankSprite.colorMul = value
        }

    var playerName = Signal<String>()

    private val actionIcon = Sprite(
        bitmap = resourcesVfs["tank/icon_rotate_left.png"].readBitmapLazy().slice()
    )
    private val numberIcon = Sprite(
        SpriteAnimation(
            spriteMap = resourcesVfs["tank/checkpoints.png"].readBitmapLazy(),
            spriteWidth = 75,
            spriteHeight = 75,
            marginTop = 0,
            marginLeft = 0,
            columns = 4,
            rows = 2,
        )
    )
    private val tankSprite = Sprite(
        SpriteAnimation(
            spriteMap = resourcesVfs["tank/tank.png"].readBitmapLazy(),
            spriteWidth = 32,
            spriteHeight = 32,
            marginTop = 0,
            marginLeft = 0,
            columns = 10,
            rows = 1,
        )
    )

    init {
        addChild(
            tankSprite.apply {
                scalePixels(75, 75)
                anchor(0.5, 0.5)
            }
        )
        // Name tag
        container {
            playerName { name ->
                removeChildren()
                text(name, textSize = 18.0)
                roundRect(width, height + 5, 10.0, fill = Colors.DARKGREY) {
                    alpha = 0.7
                    moveChildTo(this, 0)
                }
                alignTopToTopOf(this@Tank)
                centerOn(this@Tank)
            }
        }
        addChild(
            actionIcon.apply {
                scalePixels(50, 50)
                anchor(0.5, 0.5)
                alpha(0.8)
                colorMul = Colors.BLACK
                rotation = 90.degrees
                visible = false
            }
        )
        addChild(
            numberIcon.apply {
                setFrame(2)
                scalePixels(40, 40)
                anchor(0.5, 0.5)
                visible = false
            }
        )
    }

    fun playAnim() = tankSprite.playAnimationLooped()
    fun stopAnim() = tankSprite.stopAnimation()

    suspend fun move(moveOption: MoveOption, movement: Point) {
        actionIcon.bitmap = when (moveOption) {
            MoveOption.Forward -> resourcesVfs["tank/icon_up.png"]
            MoveOption.Backwards -> resourcesVfs["tank/icon_down.png"]
        }.readBitmap().slice()

        actionIcon.visible = true

        moveBy(movement)

        actionIcon.visible = false
    }

    fun setMovementIcons(moveOption: MoveOption, stepsLeft: Int) {
        tankSprite.playAnimationLooped(
            spriteDisplayTime = 150.milliseconds,
            startFrame = 0,
            reversed = moveOption == MoveOption.Backwards
        )

        actionIcon.bitmap = when (moveOption) {
            MoveOption.Forward -> resourcesVfs["tank/icon_up.png"]
            MoveOption.Backwards -> resourcesVfs["tank/icon_down.png"]
        }.readBitmapLazy().slice()
        actionIcon.visible = true

        numberIcon.rotation = -rotation
        numberIcon.visible = true
        numberIcon.setFrame(stepsLeft)
    }

    fun clearIcons() {
        numberIcon.visible = false
        actionIcon.visible = false
        tankSprite.stopAnimation()
    }

    suspend fun rotate(rot: Rotation) {
        actionIcon.bitmap = when (rot) {
            Rotation.Left -> resourcesVfs["tank/icon_rotate_left.png"]
            Rotation.Right -> resourcesVfs["tank/icon_rotate_right.png"]
            Rotation.Uturn -> resourcesVfs["tank/icon_rotate_uturn.png"]
        }.readBitmap().slice()

        actionIcon.visible = true

        tween(this::rotation[rotation + rot.angle], time = 1.seconds.takeUnless { rot == Rotation.Uturn } ?: 2.seconds)

        actionIcon.visible = false
    }
}

fun Tank.applyConfiguration(message: ConfigurePlayer) {
    if (message.color != null) {
        color = RGBA(message.color)
    }
    if (message.name != null) {
        playerName.invoke(message.name)
    }
}