import com.soywiz.kds.Array2
import com.soywiz.kmem.extract
import com.soywiz.korge.tiled.TiledMap
import com.soywiz.korma.geom.IPoint
import com.soywiz.korma.geom.Point
import com.soywiz.korma.geom.Vector2D

fun TiledMap.getTileLayerData(): List<Array2<Tile>> {
    val tiledMap = this

    fun findTileset(gid: Int) = tiledMap.tilesets.firstOrNull {
        gid in (it.data.firstgid until it.data.firstgid + it.data.tileCount)
    } ?: error("Failed to find for gid $gid")

    return tiledMap.tileLayers.map { tiles: TiledMap.Layer.Tiles ->

        Array2.withGen(tiles.width, tiles.height) { x, y ->
            val data = tiles[x, y]
            val gid = data.extract(0, 28)

            if (gid == 0) {
                return@withGen Tile("", 0, antiDiagonal = false, flipX = false, flipY = false, 0, 0)
            }

            val tiledTileset = findTileset(gid)
            val frameIndex = gid - tiledTileset.firstgid
            val tileName = tiledTileset.data.name

            Tile(
                tileSetName = tileName,
                gid = gid,
                antiDiagonal = data.extract(29),
                flipX = data.extract(31),
                flipY = data.extract(30),
                frameX = frameIndex % tiledTileset.data.columns,
                frameY = frameIndex / tiledTileset.data.columns
            )
        }
    }
}

fun Tile.transform(point: IPoint) = Point(point).let {
    if (antiDiagonal) Vector2D(-it.y, -it.x) else it
}.let {
    if (flipX) it * Vector2D(-1, 1) else it
}.let {
    if (flipY) it * Vector2D(1, -1) else it
}.let {
    // In our eyes up is ^, but computer world it is the other way
    it * Vector2D(1, -1)
}

data class Tile(
    val tileSetName: String,
    val gid: Int,
    val antiDiagonal: Boolean,
    val flipX: Boolean,
    val flipY: Boolean,
    val frameX: Int,
    val frameY: Int,
)